import React, { useState, useEffect } from 'react';
import { Container, Typography, Box, Paper, Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { authService } from '../services/authService';

interface UserInfo {
  id: number;
  username: string;
  fullName: string;
  email: string;
  phoneNumber: string;
  role: string;
  active: boolean;
}

const AccountInfo: React.FC = () => {
  const [userInfo, setUserInfo] = useState<UserInfo | null>(null);
  const navigate = useNavigate();

  // Function to load user info from localStorage
  const loadUserInfo = () => {
    const storedUserInfo = localStorage.getItem('userInfo');
    if (storedUserInfo) {
      try {
        const parsedUserInfo: UserInfo = JSON.parse(storedUserInfo);
        setUserInfo(parsedUserInfo);
      } catch (error) {
        console.error('Failed to parse user info from localStorage', error);
        setUserInfo(null); // Clear state if parsing fails
      }
    } else {
      setUserInfo(null); // Explicitly set to null if nothing in localStorage
    }
  };

  useEffect(() => {
    loadUserInfo(); // Load info on initial mount

    // Listen for changes in localStorage from other tabs/windows
    const handleStorageChange = () => {
      loadUserInfo();
    };

    window.addEventListener('storage', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, []); // Empty dependency array, runs once on mount and cleans up on unmount

  const handleLogout = () => {
    authService.logout();
    navigate('/'); // Điều hướng về trang đăng nhập sau khi đăng xuất
    // After logout, immediately update the state to reflect the change
    setUserInfo(null);
  };

  return (
    <Container maxWidth="md" sx={{ py: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Thông tin tài khoản
      </Typography>
      <Paper elevation={3} sx={{ padding: 3, mt: 3 }}>
        {userInfo ? (
          <Box>
            <Typography variant="h6" gutterBottom>
              Chào mừng, {userInfo.fullName}!
            </Typography>
            <Typography variant="body1"><strong>Tên đăng nhập:</strong> {userInfo.username}</Typography>
            <Typography variant="body1"><strong>Tên đầy đủ:</strong> {userInfo.fullName}</Typography>
            <Typography variant="body1"><strong>Email:</strong> {userInfo.email}</Typography>
            <Typography variant="body1"><strong>Số điện thoại:</strong> {userInfo.phoneNumber}</Typography>
            <Typography variant="body1"><strong>Vai trò:</strong> {userInfo.role}</Typography>
            <Box mt={3}>
              <Button
                variant="contained"
                color="secondary"
                onClick={handleLogout}
              >
                Đăng xuất
              </Button>
            </Box>
          </Box>
        ) : (
          <Box>
            <Typography variant="body1">
              Không có thông tin tài khoản để hiển thị. Vui lòng đăng nhập.
            </Typography>
            <Box mt={3}>
              <Button
                variant="contained"
                color="primary"
                onClick={() => navigate('/login')}
              >
                Đăng nhập
              </Button>
            </Box>
          </Box>
        )}
      </Paper>
    </Container>
  );
};

export default AccountInfo; 