import api, { API_ENDPOINTS } from '../api/api';

export interface HaircutService {
  id: number;
  name: string;
  description: string;
  price: number;
  durationMinutes: number;
  imageUrl: string; // Add imageUrl to interface
  active: boolean;
}

interface ApiResponse<T> {
  success: boolean;
  message: string;
  data: T;
}

export const serviceService = {
  getAllServices: async (): Promise<ApiResponse<HaircutService[]>> => {
    try {
      const response = await api.get(API_ENDPOINTS.SERVICES.LIST);
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi lấy danh sách dịch vụ');
    }
  },

  // Add other service related methods here if needed
}; 