import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { authService } from '../services/authService';

interface UserInfo {
  id: number;
  username: string;
  email: string;
  fullName?: string;
  phoneNumber?: string;
  role: 'ADMIN' | 'STAFF' | 'CUSTOMER';
  active: boolean;
}

interface AuthContextType {
  isAuthenticated: boolean;
  user: UserInfo | null;
  login: (token: string, userInfo: UserInfo) => void;
  logout: () => void;
  isAdmin: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState<UserInfo | null>(null);

  useEffect(() => {
    const token = authService.getToken();
    const userInfoString = localStorage.getItem('userInfo');

    if (token && userInfoString) {
      try {
        const userInfo: UserInfo = JSON.parse(userInfoString);
        setIsAuthenticated(true);
        setUser(userInfo);
        console.log('AuthContext useEffect: UserInfo loaded', userInfo);
      } catch (e) {
        console.error('Failed to parse user info from localStorage', e);
        authService.logout();
      }
    } else {
      setIsAuthenticated(false);
      setUser(null);
      console.log('AuthContext useEffect: No token or userInfo found');
    }
  }, []);

  const login = (token: string, userInfo: UserInfo) => {
    localStorage.setItem('authToken', token);
    localStorage.setItem('userInfo', JSON.stringify(userInfo));
    setIsAuthenticated(true);
    setUser(userInfo);
    console.log('AuthContext login: User logged in', userInfo);
  };

  const logout = () => {
    authService.logout();
    setIsAuthenticated(false);
    setUser(null);
    console.log('AuthContext logout: User logged out');
  };

  const isAdmin = user?.role === 'ADMIN';
  console.log('AuthContext render: isAdmin', isAdmin, 'User Role:', user?.role);

  return (
    <AuthContext.Provider value={{ isAuthenticated, user, login, logout, isAdmin }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 