import React from 'react';
import { Box, Typography, Button, Container } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { styled } from '@mui/material/styles';

const HeroSection = styled(Box)(({ theme }) => ({
  background: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('${process.env.PUBLIC_URL}/images/hero-bg.jpg')`,
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  color: theme.palette.common.white,
  padding: theme.spacing(15, 0),
  textAlign: 'center',
  minHeight: '500px',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
}));

const ServiceCard = styled(Box)(({ theme }) => ({
  padding: theme.spacing(3),
  borderRadius: theme.shape.borderRadius,
  boxShadow: theme.shadows[2],
  textAlign: 'center',
  transition: 'transform 0.3s',
  backgroundColor: theme.palette.background.paper,
  '&:hover': {
    transform: 'translateY(-5px)',
    boxShadow: theme.shadows[4],
  },
}));

const Home: React.FC = () => {
  const navigate = useNavigate();

  const services = [
    {
      title: "Cắt tóc",
      description: "Dịch vụ cắt tóc chuyên nghiệp với nhiều kiểu tóc hiện đại"
    },
    {
      title: "Nhuộm tóc",
      description: "Nhuộm tóc với các màu sắc đa dạng và chất lượng cao"
    },
    {
      title: "Uốn tóc",
      description: "Uốn tóc với công nghệ hiện đại, an toàn cho tóc"
    }
  ];

  return (
    <Box sx={{ flexGrow: 1 }}>
      <HeroSection>
        <Container maxWidth="md">
          <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 700 }}>
            Chào mừng đến với Booking App
          </Typography>
          <Typography variant="h5" paragraph sx={{ mb: 4 }}>
            Đặt lịch dịch vụ nhanh chóng và dễ dàng
          </Typography>
          <Button
            variant="contained"
            size="large"
            color="primary"
            onClick={() => navigate('/booking')}
            sx={{ px: 5, py: 1.5, fontSize: '1.1rem' }}
          >
            Đặt lịch ngay
          </Button>
        </Container>
      </HeroSection>

      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" component="h2" align="center" gutterBottom sx={{ fontWeight: 600 }}>
          Dịch vụ của chúng tôi
        </Typography>
        <Box sx={{ 
          display: 'grid', 
          gridTemplateColumns: { xs: '1fr', md: 'repeat(3, 1fr)' },
          gap: 4,
          mt: 2 
        }}>
          {services.map((service, index) => (
            <ServiceCard key={index}>
              <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
                {service.title}
              </Typography>
              <Typography variant="body1" color="text.secondary">
                {service.description}
              </Typography>
            </ServiceCard>
          ))}
        </Box>
      </Container>
    </Box>
  );
};

export default Home;