import React, { useEffect, useState } from 'react';
import {
  Container,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  CircularProgress,
  Box,
  Alert,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import { customerService } from '../services/customerService';
import { useAuth } from '../context/AuthContext';

interface Customer {
  id: number;
  fullName: string;
  email: string;
  phoneNumber: string;
  // Add other customer fields if necessary
}

const ManageCustomers: React.FC = () => {
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [customerToDelete, setCustomerToDelete] = useState<Customer | null>(null);

  const { isAdmin } = useAuth();

  const fetchCustomers = async () => {
    if (!isAdmin) {
      setLoading(false);
      setError('Bạn không có quyền truy cập trang này.');
      return;
    }
    try {
      setLoading(true);
      const response = await customerService.getAllCustomers();
      if (response.success) {
        setCustomers(response.data);
      } else {
        setError(response.message || 'Failed to fetch customers');
      }
    } catch (err: any) {
      setError(err.message || 'An unexpected error occurred.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCustomers();
  }, [isAdmin]);

  const handleClickOpen = (customer: Customer) => {
    setCustomerToDelete(customer);
    setOpenDialog(true);
  };

  const handleClose = () => {
    setOpenDialog(false);
    setCustomerToDelete(null);
  };

  const handleDelete = async () => {
    if (customerToDelete) {
      try {
        setLoading(true);
        const response = await customerService.deleteCustomer(customerToDelete.id);
        if (response.success) {
          setCustomers(customers.filter(c => c.id !== customerToDelete.id));
          alert('Khách hàng đã được xóa thành công!');
        } else {
          setError(response.message || 'Không thể xóa khách hàng.');
        }
      } catch (err: any) {
        setError(err.message || 'Đã xảy ra lỗi khi xóa khách hàng.');
      } finally {
        setLoading(false);
        handleClose();
      }
    }
  };

  return (
    <Container sx={{ py: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Quản lý Khách hàng
      </Typography>

      {loading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      )}

      {error && (
        <Alert severity="error" sx={{ mt: 4 }}>
          {error}
        </Alert>
      )}

      {!loading && !error && customers.length > 0 && isAdmin && (
        <TableContainer component={Paper} sx={{ mt: 4 }}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell>Họ và tên</TableCell>
                <TableCell>Email</TableCell>
                <TableCell>Số điện thoại</TableCell>
                <TableCell>Thao tác</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {customers.map((customer) => (
                <TableRow key={customer.id}>
                  <TableCell>{customer.id}</TableCell>
                  <TableCell>{customer.fullName}</TableCell>
                  <TableCell>{customer.email}</TableCell>
                  <TableCell>{customer.phoneNumber}</TableCell>
                  <TableCell>
                    <Button
                      variant="outlined"
                      color="error"
                      startIcon={<DeleteIcon />}
                      onClick={() => handleClickOpen(customer)}
                    >
                      Xóa
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      {!loading && !error && customers.length === 0 && isAdmin && (
        <Typography variant="body1" sx={{ mt: 4, textAlign: 'center' }}>
          Không có khách hàng nào để hiển thị.
        </Typography>
      )}

      <Dialog
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Xác nhận xóa khách hàng"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Bạn có chắc chắn muốn xóa khách hàng {customerToDelete?.fullName} (ID: {customerToDelete?.id}) không? Hành động này không thể hoàn tác.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Hủy
          </Button>
          <Button onClick={handleDelete} color="error" autoFocus>
            Xóa
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default ManageCustomers; 