import api, { API_ENDPOINTS } from '../api/api'; // Corrected import path

export interface Booking {
  id: number;
  customer: { id: number; fullName: string; email: string; phoneNumber: string };
  service: { name: string };
  staff: { fullName: string } | null;
  bookingDate: string; // LocalDate
  bookingTime: string; // LocalTime
  status: 'PENDING' | 'CONFIRMED' | 'COMPLETED' | 'CANCELLED';
  notes?: string;
}

interface ApiResponse<T> {
  success: boolean;
  message: string;
  data: T;
}

export const bookingService = {
  getPendingBookings: async (): Promise<ApiResponse<Booking[]>> => {
    try {
      const response = await api.get(API_ENDPOINTS.BOOKINGS.PENDING);
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi lấy danh sách lịch hẹn chờ duyệt');
    }
  },

  updateBookingStatus: async (id: number, newStatus: string): Promise<ApiResponse<Booking>> => {
    try {
      const response = await api.put(API_ENDPOINTS.BOOKINGS.UPDATE_STATUS(String(id)), { status: newStatus }, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi cập nhật trạng thái lịch hẹn');
    }
  },

  getBookingsByCustomerId: async (customerId: number): Promise<ApiResponse<Booking[]>> => {
    try {
      const response = await api.get(`${API_ENDPOINTS.BOOKINGS.BASE}/customer/${customerId}`);
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi lấy danh sách lịch hẹn của khách hàng');
    }
  },

  updateBooking: async (id: number, bookingDetails: Partial<Booking>): Promise<ApiResponse<Booking>> => {
    try {
      const response = await api.put(API_ENDPOINTS.BOOKINGS.DETAIL(String(id)), bookingDetails, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi cập nhật lịch hẹn');
    }
  },

  deleteBooking: async (id: number): Promise<ApiResponse<void>> => {
    try {
      const response = await api.delete(API_ENDPOINTS.BOOKINGS.DETAIL(String(id)));
      return response.data; // Should return success: true with no data
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi xóa lịch hẹn');
    }
  },

  // Add other booking related methods here if needed
}; 