import { api, API_ENDPOINTS } from '../api';

interface LoginRequest {
  username: string;
  password: string;
}

interface RegisterRequest {
  username: string;
  password: string;
  email: string;
  fullName: string;
}

export const authService = {
  login: async (data: LoginRequest) => {
    try {
      const response = await api.post(API_ENDPOINTS.AUTH.LOGIN, data);
      console.log('AuthService login response:', response.data); // Log full response data
      
      if (!response.data.success) {
        throw new Error(response.data.message || 'Đăng nhập thất bại. Vui lòng thử lại.');
      }

      // Lưu token vào localStorage nếu có
      if (response.data.token) {
        localStorage.setItem('authToken', response.data.token);
      }
      // Lưu thông tin người dùng vào localStorage
      if (response.data.data) {
        localStorage.setItem('userInfo', JSON.stringify(response.data.data));
      }
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Đăng nhập thất bại');
    }
  },

  register: async (data: RegisterRequest) => {
    try {
      const response = await api.post(API_ENDPOINTS.AUTH.REGISTER, data);
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Đăng ký thất bại');
    }
  },

  logout: async () => {
    try {
      const response = await api.post(API_ENDPOINTS.AUTH.LOGOUT);
      // Xóa token và userId khi đăng xuất
      localStorage.removeItem('authToken');
      localStorage.removeItem('userId');
      localStorage.removeItem('userInfo');
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Đăng xuất thất bại');
    }
  },

  // Thêm hàm kiểm tra trạng thái đăng nhập
  isAuthenticated: () => {
    return !!localStorage.getItem('authToken');
  },

  // Thêm hàm lấy token
  getToken: () => {
    return localStorage.getItem('authToken');
  },
}; 