import React from 'react';
import { Container, Typography, Box, Button } from '@mui/material';
import { useLocation, useNavigate } from 'react-router-dom';

const AppointmentNotifications: React.FC = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const booking = location.state?.booking; // Lấy thông tin booking từ state của navigate

  const handleViewMyBookings = () => {
    if (booking?.customer?.id) {
      navigate(`/my-bookings/${booking.customer.id}`);
    } else if (booking?.email) {
      // Fallback: if customer ID not directly available, try to navigate to general my-bookings.
      // In a real app, you might want to fetch customer ID by email or require login.
      navigate('/my-bookings');
    } else {
      navigate('/my-bookings'); // Navigate to default my-bookings if no specific ID or email
    }
  };

  return (
    <Container sx={{ py: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Thông báo lịch hẹn
      </Typography>
      <Box>
        {booking ? (
          <>
            <Typography variant="h6" component="h2" gutterBottom>
              Lịch đặt của bạn đã thành công!
            </Typography>
            <Typography variant="body1"><strong>Dịch vụ:</strong> {booking.service?.name || booking.service}</Typography>
            <Typography variant="body1"><strong>Ngày:</strong> {new Date(booking.bookingDate).toLocaleDateString('vi-VN')}</Typography>
            <Typography variant="body1"><strong>Thời gian:</strong> {booking.displayTime || booking.bookingTime}</Typography>
            <Typography variant="body1"><strong>Tên khách hàng:</strong> {booking.customer?.fullName || booking.name}</Typography>
            <Typography variant="body1"><strong>Số điện thoại:</strong> {booking.customer?.phoneNumber || booking.phone}</Typography>
            <Typography variant="body1"><strong>Email:</strong> {booking.customer?.email || booking.email}</Typography>
            {booking.notes && <Typography variant="body1"><strong>Ghi chú:</strong> {booking.notes}</Typography>}
            <Typography variant="body1"><strong>Trạng thái:</strong> {booking.status === 'CONFIRMED' ? 'Đã xác nhận' : booking.status === 'PENDING' ? 'Chờ xác nhận' : booking.status === 'CANCELLED' ? 'Đã hủy' : booking.status}</Typography>
            <Box mt={4}>
              <Button
                variant="contained"
                color="primary"
                onClick={handleViewMyBookings}
              >
                Xem lịch của tôi
              </Button>
            </Box>
          </>
        ) : (
          <>
            <Typography variant="body1">
              Đây là trang hiển thị thông báo về lịch hẹn. Không có thông tin lịch hẹn cụ thể.
            </Typography>
            <Box mt={4}>
              <Button
                variant="contained"
                color="primary"
                onClick={handleViewMyBookings}
              >
                Xem lịch của tôi
              </Button>
            </Box>
          </>
        )}
      </Box>
    </Container>
  );
};

export default AppointmentNotifications; 