import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  TextField,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Box,
  Paper,
  SelectChangeEvent,
} from '@mui/material';
import { DatePicker, TimePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { vi } from 'date-fns/locale';
import styled from '@emotion/styled';
import { api, API_ENDPOINTS } from '../api';

const BookingForm = styled(Paper)({
  padding: 24,
  marginTop: 24,
});

interface FormData {
  service: string;
  date: Date | null;
  time: Date | null;
  name: string;
  phone: string;
  email: string;
  notes: string;
}

const Booking: React.FC = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState<FormData>({
    service: '',
    date: null,
    time: null,
    name: '',
    phone: '',
    email: '',
    notes: '',
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.date || !formData.time) {
      alert('Vui lòng chọn ngày và giờ');
      return;
    }

    const combinedDateTime = new Date(formData.date);
    combinedDateTime.setHours(formData.time.getHours());
    combinedDateTime.setMinutes(formData.time.getMinutes());
    combinedDateTime.setSeconds(0);
    combinedDateTime.setMilliseconds(0);

    const payload = {
      service: formData.service,
      datetime: combinedDateTime.toISOString(),
      name: formData.name,
      phone: formData.phone,
      email: formData.email,
      notes: formData.notes || '',
    };

    try {
      const response = await api.post(API_ENDPOINTS.BOOKINGS.BASE, payload);
      console.log('Booking success:', response.data);
      alert('Đặt lịch thành công!');

      // Lấy thông tin lịch hẹn vừa tạo để truyền sang trang thông báo
      const newBookingDetails = response.data.data; 

      // Add the correctly formatted time to newBookingDetails
      // Ensure formData.time is not null before using it
      if (formData.time) {
        // Format time to HH:mm string in local timezone
        const formattedTime = formData.time.toLocaleTimeString('vi-VN', { hour: '2-digit', minute: '2-digit', hour12: false });
        newBookingDetails.displayTime = formattedTime;
      } else {
        newBookingDetails.displayTime = ''; // Set to empty string or handle as appropriate
      }

      // Chuyển hướng đến trang thông báo lịch hẹn và truyền dữ liệu
      navigate('/appointment-notifications', { state: { booking: newBookingDetails } });

      setFormData({
        service: '',
        date: null,
        time: null,
        name: '',
        phone: '',
        email: '',
        notes: '',
      });
    } catch (error: any) {
      console.error('Booking failed:', error);
      const message =
        error.response?.data?.message || 'Đặt lịch thất bại. Vui lòng thử lại.';
      alert(message);
    }
  };

  return (
    <Container maxWidth="md">
      <Typography variant="h4" component="h1" gutterBottom>
        Đặt lịch dịch vụ
      </Typography>
      <BookingForm elevation={3}>
        <form onSubmit={handleSubmit}>
          <Box sx={{ display: 'grid', gap: 3 }}>
            <Box>
              <FormControl fullWidth required>
                <InputLabel>Dịch vụ</InputLabel>
                <Select
                  name="service"
                  value={formData.service}
                  onChange={handleChange}
                  label="Dịch vụ"
                >
                  <MenuItem value="Cắt tóc">Cắt tóc</MenuItem>
                  <MenuItem value="Nhuộm tóc">Nhuộm tóc</MenuItem>
                  <MenuItem value="Uốn tóc">Uốn tóc</MenuItem>
                  <MenuItem value="Gội đầu">Gội đầu</MenuItem>
                  <MenuItem value="Tạo kiểu">Tạo kiểu</MenuItem>
                  <MenuItem value="Phục hồi tóc">Phục hồi tóc</MenuItem>
                </Select>
              </FormControl>
            </Box>

            <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={vi}>
              <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 3 }}>
                <DatePicker
                  label="Ngày"
                  value={formData.date}
                  onChange={(newValue) =>
                    setFormData((prev) => ({ ...prev, date: newValue }))
                  }
                  slotProps={{
                    textField: {
                      fullWidth: true,
                      required: true,
                    },
                  }}
                />

                <TimePicker
                  label="Giờ"
                  value={formData.time}
                  onChange={(newValue) =>
                    setFormData((prev) => ({ ...prev, time: newValue }))
                  }
                  slotProps={{
                    textField: {
                      fullWidth: true,
                      required: true,
                    },
                  }}
                />
              </Box>
            </LocalizationProvider>

            <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', md: '1fr 1fr' }, gap: 3 }}>
              <TextField
                fullWidth
                label="Họ và tên"
                name="name"
                value={formData.name}
                onChange={handleChange}
                required
              />

              <TextField
                fullWidth
                label="Số điện thoại"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                required
              />
            </Box>

            <Box>
              <TextField
                fullWidth
                label="Email"
                name="email"
                type="email"
                value={formData.email}
                onChange={handleChange}
                required
              />
            </Box>

            <Box>
              <TextField
                fullWidth
                label="Ghi chú"
                name="notes"
                value={formData.notes}
                onChange={handleChange}
                multiline
                rows={4}
              />
            </Box>

            <Box>
              <Button type="submit" variant="contained" color="primary" size="large" fullWidth>
                Đặt lịch
              </Button>
            </Box>
          </Box>
        </form>
      </BookingForm>
    </Container>
  );
};

export default Booking;
