import React from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Container,
  Box,
  Badge,
} from '@mui/material';
import { Link as RouterLink, useNavigate, Outlet } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import { authService } from '../services/authService';
import { useAuth } from '../context/AuthContext';

const StyledAppBar = styled(AppBar)(({ theme }) => ({
  marginBottom: theme.spacing(4),
}));

const StyledToolbar = styled(Toolbar)({
  display: 'flex',
  justifyContent: 'space-between',
});

const NavLinks = styled(Box)({
  display: 'flex',
  gap: '1rem',
});

const Footer = styled('footer')(({ theme }) => ({
  marginTop: 'auto',
  padding: theme.spacing(3),
  backgroundColor: theme.palette.primary.main,
  color: theme.palette.primary.contrastText,
  textAlign: 'center',
}));

interface LayoutProps {
  // children: React.ReactNode; // No longer needed
}

const Layout: React.FC<LayoutProps> = () => {
  const [notificationCount, setNotificationCount] = React.useState(0);
  const navigate = useNavigate();
  const { isAdmin } = useAuth();

  const handleLogout = () => {
    authService.logout();
    navigate('/');
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <StyledAppBar position="static">
        <StyledToolbar>
          <Typography
            variant="h6"
            component={RouterLink}
            to="/"
            sx={{ textDecoration: 'none', color: 'inherit' }}
          >
            Booking App
          </Typography>
          <NavLinks>
            <Button component={RouterLink} to="/services" color="inherit">
              Dịch vụ
            </Button>
            <Button component={RouterLink} to="/booking" color="inherit">
              Đặt lịch
            </Button>
            <Button component={RouterLink} to="/my-bookings" color="inherit">
              <Badge badgeContent={notificationCount} color="error">
                Lịch của tôi
              </Badge>
            </Button>
            <Button
              component={RouterLink}
              to="/appointment-notifications"
              color="inherit"
            >
              Thông báo lịch hẹn
            </Button>
            {isAdmin && (
              <Button component={RouterLink} to="/admin-dashboard" color="inherit">
                Bảng điều khiển Admin
              </Button>
            )}
            <Button component={RouterLink} to="/account-info" color="inherit">
              Thông tin tài khoản
            </Button>
          </NavLinks>
        </StyledToolbar>
      </StyledAppBar>

      <Container component="main" sx={{ flex: 1 }}>
        <Outlet />
      </Container>

      <Footer>
        <Typography variant="body2">
          © {new Date().getFullYear()} Booking App. All rights reserved.
        </Typography>
      </Footer>
    </Box>
  );
};

export default Layout;
