import React, { useEffect, useState } from 'react';
import {
  Container,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  CircularProgress,
  Box,
  Alert,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@mui/material';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CancelIcon from '@mui/icons-material/Cancel';
import { bookingService } from '../services/bookingService';
import { useAuth } from '../context/AuthContext';

interface Booking {
  id: number;
  customer: { fullName: string; email: string; phoneNumber: string };
  service: { name: string };
  staff: { fullName: string } | null; // Staff might be null initially
  bookingDate: string; // LocalDate
  bookingTime: string; // LocalTime
  status: 'PENDING' | 'CONFIRMED' | 'COMPLETED' | 'CANCELLED';
  notes?: string;
}

const PendingBookingsPage: React.FC = () => {
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [openDialog, setOpenDialog] = useState(false);
  const [bookingToProcess, setBookingToProcess] = useState<Booking | null>(null);
  const [dialogAction, setDialogAction] = useState<'confirm' | 'cancel' | null>(null);

  const { isAdmin } = useAuth();

  const fetchPendingBookings = async () => {
    if (!isAdmin) {
      setLoading(false);
      setError('Bạn không có quyền truy cập trang này.');
      return;
    }
    try {
      setLoading(true);
      const response = await bookingService.getPendingBookings();
      if (response.success) {
        setBookings(response.data);
      } else {
        setError(response.message || 'Failed to fetch pending bookings');
      }
    } catch (err: any) {
      setError(err.message || 'An unexpected error occurred.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPendingBookings();
  }, [isAdmin]);

  const handleClickOpen = (booking: Booking, action: 'confirm' | 'cancel') => {
    setBookingToProcess(booking);
    setDialogAction(action);
    setOpenDialog(true);
  };

  const handleClose = () => {
    setOpenDialog(false);
    setBookingToProcess(null);
    setDialogAction(null);
  };

  const handleProcessBooking = async () => {
    if (bookingToProcess && dialogAction) {
      try {
        setLoading(true);
        let response;
        if (dialogAction === 'confirm') {
          response = await bookingService.updateBookingStatus(bookingToProcess.id, 'CONFIRMED');
        } else {
          // dialogAction === 'cancel'
          response = await bookingService.updateBookingStatus(bookingToProcess.id, 'CANCELLED');
        }

        if (response.success) {
          alert(`Lịch hẹn đã được ${dialogAction === 'confirm' ? 'duyệt' : 'hủy'} thành công!`);
          fetchPendingBookings(); // Refresh the list
        } else {
          setError(response.message || `Không thể ${dialogAction === 'confirm' ? 'duyệt' : 'hủy'} lịch hẹn.`);
        }
      } catch (err: any) {
        setError(err.message || `Đã xảy ra lỗi khi ${dialogAction === 'confirm' ? 'duyệt' : 'hủy'} lịch hẹn.`);
      } finally {
        setLoading(false);
        handleClose();
      }
    }
  };

  return (
    <Container sx={{ py: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Quản lý Lịch hẹn Chờ duyệt
      </Typography>

      {loading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      )}

      {error && (
        <Alert severity="error" sx={{ mt: 4 }}>
          {error}
        </Alert>
      )}

      {!loading && !error && bookings.length > 0 && isAdmin && (
        <TableContainer component={Paper} sx={{ mt: 4 }}>
          <Table sx={{ minWidth: 650 }} aria-label="pending bookings table">
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell>Khách hàng</TableCell>
                <TableCell>Dịch vụ</TableCell>
                <TableCell>Nhân viên</TableCell>
                <TableCell>Ngày</TableCell>
                <TableCell>Giờ</TableCell>
                <TableCell>Ghi chú</TableCell>
                <TableCell>Trạng thái</TableCell>
                <TableCell>Thao tác</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {bookings.map((booking) => (
                <TableRow key={booking.id}>
                  <TableCell>{booking.id}</TableCell>
                  <TableCell>{booking.customer.fullName}</TableCell>
                  <TableCell>{booking.service.name}</TableCell>
                  <TableCell>{booking.staff ? booking.staff.fullName : '-'}</TableCell>
                  <TableCell>{booking.bookingDate}</TableCell>
                  <TableCell>{booking.bookingTime}</TableCell>
                  <TableCell>{booking.notes || '-'}</TableCell>
                  <TableCell>{booking.status}</TableCell>
                  <TableCell>
                    <Button
                      variant="outlined"
                      color="success"
                      startIcon={<CheckCircleIcon />}
                      onClick={() => handleClickOpen(booking, 'confirm')}
                      sx={{ mr: 1 }}
                    >
                      Duyệt
                    </Button>
                    <Button
                      variant="outlined"
                      color="error"
                      startIcon={<CancelIcon />}
                      onClick={() => handleClickOpen(booking, 'cancel')}
                    >
                      Từ chối
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      {!loading && !error && bookings.length === 0 && isAdmin && (
        <Typography variant="body1" sx={{ mt: 4, textAlign: 'center' }}>
          Không có lịch hẹn chờ duyệt.
        </Typography>
      )}

      <Dialog
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {dialogAction === 'confirm' ? "Xác nhận duyệt lịch hẹn" : "Xác nhận hủy lịch hẹn"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Bạn có chắc chắn muốn {dialogAction === 'confirm' ? 'duyệt' : 'hủy'} lịch hẹn của khách hàng {bookingToProcess?.customer.fullName} (ID: {bookingToProcess?.id}) không? Hành động này sẽ cập nhật trạng thái lịch hẹn.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Hủy
          </Button>
          <Button onClick={handleProcessBooking} color={dialogAction === 'confirm' ? 'success' : 'error'} autoFocus>
            {dialogAction === 'confirm' ? 'Duyệt' : 'Hủy'}
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default PendingBookingsPage; 