import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Layout from './components/Layout';
import AuthLayout from './components/AuthLayout';
import ProtectedRoute from './components/ProtectedRoute';
import ProtectedRouteForAdmin from './components/ProtectedRouteForAdmin';
import Home from './pages/Home';
import Services from './pages/Services';
import Booking from './pages/Booking';
import MyBookings from './pages/MyBookings';
import AppointmentNotifications from './pages/AppointmentNotifications';
import Login from './pages/Login';
import Register from './pages/Register';
import AccountInfo from './pages/AccountInfo';
import AdminDashboard from './pages/AdminDashboard';
import ManageStaff from './pages/ManageStaff';
import ManageCustomers from './pages/ManageCustomers';
import PendingBookingsPage from './pages/PendingBookingsPage';
import { AuthProvider } from './context/AuthContext';


const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AuthProvider>
        <Routes>
          <Route path="/" element={<Navigate to="/login" replace />} />
          <Route element={<AuthLayout />}>
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
          </Route>
          <Route element={<Layout />}>
            <Route element={<ProtectedRoute />}>
              <Route path="/home" element={<Home />} />
              <Route path="/services" element={<Services />} />
              <Route path="/booking" element={<Booking />} />
              <Route path="/my-bookings" element={<MyBookings />} />
              <Route path="/my-bookings/:customerId" element={<MyBookings />} />
              <Route path="/appointment-notifications" element={<AppointmentNotifications />} />
              <Route path="/account-info" element={<AccountInfo />} />
            </Route>
            <Route element={<ProtectedRouteForAdmin />}>
              <Route path="/admin-dashboard" element={<AdminDashboard />} />
              <Route path="/admin/staff" element={<ManageStaff />} />
              <Route path="/admin/customers" element={<ManageCustomers />} />
              <Route path="/admin/pending-bookings" element={<PendingBookingsPage />} />
            </Route>
          </Route>
        </Routes>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;
