const API_BASE_URL = 'http://localhost:8080'; // Thay thế bằng URL API backend thực tế

interface BookingData {
  service: string;
  date: string; // Hoặc kiểu Date nếu bạn xử lý ở frontend
  time: string; // Hoặc kiểu Date nếu bạn xử lý ở frontend
  name: string;
  phone: string;
  email: string;
  notes?: string;
}

function getAuthHeader(): Record<string, string> {
  const token = localStorage.getItem('authToken');
  if (token) {
    return { 'Authorization': `Bearer ${token}` };
  } else {
    return {}; // Return empty object if no token
  }
}

export async function createBooking(bookingData: BookingData): Promise<any> {
  try {
    const response = await fetch(`${API_BASE_URL}/bookings`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        ...getAuthHeader()
      },
      body: JSON.stringify(bookingData),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || 'Đã xảy ra lỗi khi đặt lịch');
    }

    const data = await response.json();
    return data;

  } catch (error) {
    console.error('Error creating booking:', error);
    throw error;
  }
}

export async function getMyBookings(customerId: number): Promise<any> {
  try {
    // Replace '/api/bookings/mybookings' with your actual backend endpoint for fetching user bookings
    const response = await fetch(`${API_BASE_URL}/api/bookings/customer/${customerId}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        ...getAuthHeader()
      },
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || 'Đã xảy ra lỗi khi lấy danh sách đặt lịch');
    }

    const data = await response.json();
    return data.data;

  } catch (error) {
    console.error('Error fetching user bookings:', error);
    throw error;
  }
}

