export async function updateBooking(bookingId: number, bookingData: any): Promise<any> {
  try {
    const response = await fetch(`${API_BASE_URL}/api/bookings/${bookingId}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        ...getAuthHeader()
      },
      body: JSON.stringify(bookingData),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || 'Đã xảy ra lỗi khi cập nhật lịch đặt');
    }

    const data = await response.json();
    return data;

  } catch (error) {
    console.error('Error updating booking:', error);
    throw error;
  }
}

export async function deleteBooking(bookingId: number): Promise<void> {
  try {
    const response = await fetch(`${API_BASE_URL}/api/bookings/${bookingId}`, {
      method: 'DELETE',
      headers: getAuthHeader(),
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || 'Đã xảy ra lỗi khi xóa lịch đặt');
    }

    // Assuming the backend returns nothing or a success indicator on successful deletion
    // If your backend returns JSON confirmation, you can process it here

  } catch (error) {
    console.error('Error deleting booking:', error);
    throw error;
  }
} 