import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import {
  Container,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Box,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import { getMyBookings, deleteBooking } from '../api/bookingApi';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import TextField from '@mui/material/TextField';

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
}));

const MyBookings: React.FC = () => {
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [openEditModal, setOpenEditModal] = useState(false);
  const [editingBooking, setEditingBooking] = useState<any>(null);
  const { customerId: urlCustomerId } = useParams<{ customerId: string }>(); // Lấy customerId từ URL

  const fetchBookings = async () => {
    try {
      let idToFetch: number | null = null;

      if (urlCustomerId) {
        // Ưu tiên customerId từ URL
        idToFetch = parseInt(urlCustomerId, 10);
      } else {
        // Fallback về userId từ localStorage nếu không có trong URL
        const userId = localStorage.getItem('userId');
        if (userId) {
          idToFetch = parseInt(userId, 10);
        }
      }

      if (idToFetch) {
        const data = await getMyBookings(idToFetch);
        setBookings(data);
        setLoading(false);
      } else {
        setLoading(false);
        setError('Không tìm thấy ID khách hàng. Vui lòng đăng nhập hoặc đặt lịch.');
      }
    } catch (err: any) {
      setError(err.message);
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchBookings();
  }, [urlCustomerId]); // Thêm urlCustomerId vào dependency array để re-fetch khi ID thay đổi

  if (loading) {
    return <Container sx={{ py: 8 }}><Typography>Đang tải...</Typography></Container>;
  }

  if (error) {
    return <Container sx={{ py: 8 }}><Typography color="error">Lỗi: {error}</Typography></Container>;
  }

  const handleEditClick = (booking: any) => {
    setEditingBooking(booking);
    setOpenEditModal(true);
  };

  const handleCloseEditModal = () => {
    setEditingBooking(null);
    setOpenEditModal(false);
  };

  const handleSaveEdit = () => {
    // Implement the logic to save the edited booking
    console.log('Saving edited booking:', editingBooking);
    handleCloseEditModal();
  };

  const handleDeleteBooking = async (bookingId: number) => {
    try {
      await deleteBooking(bookingId);
      alert('Lịch đặt đã được hủy thành công!');
      fetchBookings(); // Refresh the bookings list after successful deletion
    } catch (err: any) {
      console.error('Error deleting booking:', err);
      alert(`Lỗi khi hủy lịch đặt: ${err.message}`);
    }
  };

  return (
    <Container sx={{ py: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Lịch của tôi
      </Typography>

      {bookings.length === 0 ? (
        <Typography>Bạn chưa có lịch đặt nào.</Typography>
      ) : (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Dịch vụ</TableCell>
                <TableCell>Ngày</TableCell>
                <TableCell>Giờ</TableCell>
                <TableCell>Trạng thái</TableCell>
                <TableCell>Thao tác</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {bookings.map((booking: any) => (
                <StyledTableRow key={booking.id}>
                  <TableCell>{booking.service.name}</TableCell> {/* Assuming service has a 'name' field */}
                  <TableCell>{booking.date}</TableCell>
                  <TableCell>{booking.time}</TableCell>
                  <TableCell>
                    <Box
                      sx={{
                        display: 'inline-block',
                        padding: '4px 8px',
                        borderRadius: '4px',
                        backgroundColor:
                          booking.status === 'CONFIRMED'
                            ? 'success.light'
                            : booking.status === 'PENDING'
                            ? 'warning.light'
                            : 'error.light', // Assuming backend status values are 'CONFIRMED', 'PENDING', 'CANCELLED'
                        color: 'white',
                      }}
                    >
                      {booking.status === 'CONFIRMED' ? 'Đã xác nhận' : booking.status === 'PENDING' ? 'Chờ xác nhận' : booking.status === 'CANCELLED' ? 'Đã hủy' : booking.status}
                    </Box>
                  </TableCell>
                  <TableCell>
                    {booking.status === 'PENDING' && (
                      <Button
                        variant="outlined"
                        color="error"
                        size="small"
                        onClick={() => handleDeleteBooking(booking.id)}
                      >
                        Hủy lịch
                      </Button>
                    )}
                    <Button
                      variant="outlined"
                      size="small"
                      sx={{ ml: 1 }}
                      onClick={() => handleEditClick(booking)}
                    >
                      Chỉnh sửa
                    </Button>
                  </TableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <Dialog open={openEditModal} onClose={handleCloseEditModal}>
        <DialogTitle>Chỉnh sửa lịch đặt</DialogTitle>
        <DialogContent>
          {editingBooking && (
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, pt: 1 }}>
              <TextField
                label="Dịch vụ"
                fullWidth
                value={editingBooking.service?.name || ''}
                disabled
              />
              <TextField
                label="Ngày"
                type="date"
                fullWidth
                value={editingBooking.date || ''}
                onChange={(e) => setEditingBooking({ ...editingBooking, date: e.target.value })}
                InputLabelProps={{ shrink: true }}
              />
              <TextField
                label="Giờ"
                type="time"
                fullWidth
                value={editingBooking.time || ''}
                onChange={(e) => setEditingBooking({ ...editingBooking, time: e.target.value })}
                InputLabelProps={{ shrink: true }}
              />
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseEditModal}>Hủy</Button>
          <Button onClick={handleSaveEdit}>Lưu</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default MyBookings; 