import { api, API_ENDPOINTS } from '../api';

interface Customer {
  id: number;
  fullName: string;
  email: string;
  phoneNumber: string;
}

interface ApiResponse<T> {
  success: boolean;
  message: string;
  data: T;
}

export const customerService = {
  getAllCustomers: async (): Promise<ApiResponse<Customer[]>> => {
    try {
      const response = await api.get(API_ENDPOINTS.CUSTOMERS.GET_ALL);
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi lấy danh sách khách hàng');
    }
  },

  deleteCustomer: async (id: number): Promise<ApiResponse<void>> => {
    try {
      const response = await api.delete(API_ENDPOINTS.CUSTOMERS.DELETE(String(id)));
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || 'Lỗi khi xóa khách hàng');
    }
  },

  // Add other customer related methods here if needed (e.g., create, update, delete)
}; 